$(document).ready(function() {

    //Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.menu').toggleClass('menu--active')
        $('body').toggleClass('fixed-body')
    })

    //Form-btn
    $('.form-btn').click(function () {
        let form = $('.connection__wrap');
        let formModal = $('.form-modal');
        $(this).parents('.connection__wrap').addClass('connection__wrap--active')
        $(this).parents('.connection').find('.form-modal').addClass('form-modal--active')
        setTimeout(function() {
            form.removeClass('connection__wrap--active');
            formModal.removeClass('form-modal--active');
        }, 6000);
    })

    //Animation
    gsap.registerPlugin(ScrollTrigger);

    //main
    const tl = gsap.timeline({defaults: {duration: 0.7}})
    tl.from('.main__group', {opacity: 0, x: -150})
        .from('.main__img', {opacity: 0, x: 150})
        .from('.main__group2', {opacity: 0, x: -150})



})

